const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');

export function hexToRgb(hex) {
  const result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
  return result ? {
    r: parseInt(result[1], 16),
    g: parseInt(result[2], 16),
    b: parseInt(result[3], 16),
  } : null;
}

export function paintBucket() {
  ctx.fillStyle = document.querySelector('.color-label').getAttribute('value');
  ctx.fillRect(0, 0, canvas.width, canvas.height);
}

export function paintSamePixels(e) {
  const target = ctx.getImageData(e.offsetX / 5, e.offsetY / 5, 1, 1).data.slice(0, 3);
  const bg1 = [76, 76, 76];
  const bg2 = [85, 85, 85];
  const activeColor = document.querySelector('.color-label1').getAttribute('value');
  const activeColorRgb = hexToRgb(activeColor);
  const imgData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  const { data } = imgData;

  for (let i = 0; i < data.length; i += 4) {
    if ((data[i] === bg1[0] && data[i + 1] === bg1[1] && data[i + 2] === bg1[2])
    || (data[i] === bg2[0] && data[i + 1] === bg2[1] && data[i + 2] === bg2[2])
    || (data[i] === target[0] && data[i + 1] === target[1] && data[i + 2] === target[2])) {
      data[i] = activeColorRgb.r;
      data[i + 1] = activeColorRgb.g;
      data[i + 2] = activeColorRgb.b;
    }
  }

  ctx.putImageData(imgData, 0, 0);
}
