const KeyboardEvent = {
  A: 65,
  B: 66,
  C: 67,
  D: 68,
  E: 69,
  F: 70,
  J: 74,
  L: 76,
  N: 78,
  O: 79,
  P: 80,
  R: 82,
  S: 83,
  T: 84,
  W: 87,
  X: 88,
  Z: 90,
};

function activateTool(tool) {
  document.querySelectorAll('.tools-item').forEach(i => i.classList.remove('active-tool'));
  tool.classList.add('active-tool');
}

export default function keyboardShortcuts() {
  document.addEventListener('keyup', (event) => {
    if (event.keyCode === KeyboardEvent.P) activateTool(document.querySelector('.pen'));
    if (event.keyCode === KeyboardEvent.B) activateTool(document.querySelector('.bucket'));
    if (event.keyCode === KeyboardEvent.A) activateTool(document.querySelector('.paint-same'));
    if (event.keyCode === KeyboardEvent.C) activateTool(document.querySelector('.circle'));
    if (event.keyCode === KeyboardEvent.R) activateTool(document.querySelector('.rect'));
    if (event.keyCode === KeyboardEvent.E) activateTool(document.querySelector('.eraser'));
    if (event.keyCode === KeyboardEvent.L) activateTool(document.querySelector('.line'));
    if (event.keyCode === KeyboardEvent.T) activateTool(document.querySelector('.triangle'));
    if (event.keyCode === KeyboardEvent.S) activateTool(document.querySelector('.semicircle-top'));
    if (event.keyCode === KeyboardEvent.J) activateTool(document.querySelector('.semicircle-bottom'));
    if (event.keyCode === KeyboardEvent.O) activateTool(document.querySelector('.rhombus'));
    if (event.keyCode === KeyboardEvent.D) activateTool(document.querySelector('.darken'));
    if (event.keyCode === KeyboardEvent.N) activateTool(document.querySelector('.lighten'));
    if (event.keyCode === KeyboardEvent.Z) activateTool(document.querySelector('.dithering'));
    if (event.keyCode === KeyboardEvent.X) activateTool(document.querySelector('.picker'));
    if (event.keyCode === KeyboardEvent.W) document.querySelector('.swap-colors').click();
    if (event.keyCode === KeyboardEvent.F) document.querySelector('.full-screen-img').click();
  });
}
