export const frames = [];
export const framesCount = { count: 0 };

const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');

export function updateFramesNumbers() {
  let canvasNumber = 1;
  let frameNumber = 1;

  document.querySelectorAll('.frame-canvas').forEach((i) => {
    i.setAttribute('number', `${canvasNumber}`);
    canvasNumber += 1;
  });

  document.querySelectorAll('.frame-number').forEach((i) => {
    const item = i;
    item.innerHTML = `${frameNumber}`;
    frameNumber += 1;
  });
}

export function frameIsBeingEdited(target) {
  const activeFrame = document.querySelector('.active-frame');

  if (target.classList.contains('last-frame')) {
    canvas.classList.remove('edited');
    if (activeFrame) activeFrame.classList.remove('active-frame');
    target.classList.add('active-frame');
  } else {
    canvas.classList.add('edited');
    if (activeFrame) activeFrame.classList.remove('active-frame');
    target.classList.add('active-frame');
  }

  const context = target.getContext('2d');
  const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
  ctx.putImageData(imageData, 0, 0);
}

export function clear(cntx) {
  const context = cntx;
  const img = document.querySelector('.bg');
  const pattern = context.createPattern(img, 'repeat');
  context.fillStyle = pattern;
  context.fillRect(0, 0, canvas.width, canvas.height);
  context.beginPath();
  context.fillStyle = 'black';
}

export function addContextForActiveFrame() {
  const frame = document.querySelector('.active-frame');
  if (!frame) return;
  const context = frame.getContext('2d');
  frame.width = 128;
  frame.height = 128;
  const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  context.putImageData(imageData, 0, 0);
}
