const fpsSlider = document.querySelector('.fps-slider');
const encoder = new GIFEncoder();
encoder.setRepeat(0);

function downloadGif() {
  encoder.setDelay(Math.floor(1000 / fpsSlider.value));
  encoder.start();

  const arr = Array.from(document.querySelectorAll('.frame-canvas'));
  for (let i = 0; i < arr.length; i += 1) {
    const temp = document.createElement('canvas');
    temp.classList.add('temp-canvas');
    temp.width = 320;
    temp.height = 320;
    temp.style.imageRendering = 'pixelated';
    document.body.appendChild(temp);

    const context = arr[i].getContext('2d');
    const imageData = context.getImageData(0, 0, 128, 128);
    const tempContext = temp.getContext('2d');
    tempContext.putImageData(imageData, 0, 0);
    tempContext.scale(2.5, 2.5);
    tempContext.drawImage(temp, 0, 0);
    encoder.addFrame(tempContext);
    document.body.removeChild(temp);
  }
  encoder.finish();
  encoder.download('Piskel Clone.gif');
}

export default function gifDownload() {
  document.querySelector('.download-gif').addEventListener('click', downloadGif);
}
