import updateInterval from '../preview/preview';
import { frameIsBeingEdited, addContextForActiveFrame } from './editFrames';
import { paintSamePixels, paintBucket } from '../tools/bucket';
import {
  pushTemporaryFrame, pushFrame, addFrameToFramesList, cloneFrame, deleteFrame,
} from './frames';

const canvas = document.querySelector('.canvas');
canvas.width = 128;
canvas.height = 128;
const btn = document.querySelector('.add');
const framesList = document.querySelector('.frames-list');

export default function framesEvents() {
  canvas.addEventListener('mouseup', pushTemporaryFrame);

  framesList.addEventListener('click', (e) => {
    if (e.target.classList.contains('frame-canvas')) frameIsBeingEdited(e.target);
  });

  btn.addEventListener('click', () => {
    updateInterval();
    addContextForActiveFrame();
    pushFrame();
    addFrameToFramesList();
  });

  document.addEventListener('click', (e) => {
    if (e.target.classList.contains('clone')) {
      const node = e.target.parentNode.cloneNode(true);
      const context = e.target.parentNode.firstChild.getContext('2d');
      cloneFrame(node, context);
    }
  });

  document.addEventListener('click', (e) => {
    if (e.target.classList.contains('bin')) {
      const node = e.target.parentNode;
      deleteFrame(node);
    }
  });

  canvas.addEventListener('click', () => {
    if (document.querySelector('.bucket').classList.contains('active-tool')) {
      paintBucket();
      pushTemporaryFrame();
    }
  });

  canvas.addEventListener('click', (e) => {
    if (document.querySelector('.paint-same').classList.contains('active-tool')) {
      paintSamePixels(e);
      pushTemporaryFrame();
    }
  });
}
