const input1 = document.getElementById('choose-color1');
const label1 = document.querySelector('.color-label1');
const input2 = document.getElementById('choose-color2');
const label2 = document.querySelector('.color-label2');
const swap = document.querySelector('.swap-colors');
const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');

export function componentToHex(c) {
  const hex = c.toString(16);
  return hex.length === 1 ? `0${hex}` : hex;
}

export function rgbToHex(rgb) {
  return `#${componentToHex(rgb[0])}${componentToHex(rgb[1])}${componentToHex(rgb[2])}`;
}

function getPixel(x, y) {
  const imgd = ctx.getImageData(x, y, 1, 1);
  const rgb = imgd.data.slice(0, 3);
  return rgbToHex(rgb);
}

function updateColors(pickedColor) {
  label2.setAttribute('value', label1.getAttribute('value'));
  label2.style.backgroundColor = label1.style.backgroundColor;
  label1.setAttribute('value', pickedColor);
  label1.style.backgroundColor = pickedColor;
}

export default function colorPicker() {
  input1.addEventListener('input', () => {
    label1.style.backgroundColor = input1.value;
    label1.setAttribute('value', `${input1.value}`);
  });

  input2.addEventListener('input', () => {
    label2.style.backgroundColor = input2.value;
    label2.setAttribute('value', `${input2.value}`);
  });

  swap.addEventListener('click', () => {
    const color1 = label1.getAttribute('value');
    const color2 = label2.getAttribute('value');
    label1.setAttribute('value', color2);
    label2.setAttribute('value', color1);
    label1.style.backgroundColor = color2;
    label2.style.backgroundColor = color1;
  });

  canvas.addEventListener('click', (e) => {
    if (document.querySelector('.picker').classList.contains('active-tool')) {
      updateColors(getPixel(e.offsetX / 5, e.offsetY / 5));
    }
  });
}
