import { frames, clear } from '../frames/editFrames';


const canvas2 = document.querySelector('.canvas2');
const ctx2 = canvas2.getContext('2d');
const fpsSlider = document.querySelector('.fps-slider');
const timeouts = [];
let interval;
export const intervalTime = { value: 100 };
export const timeoutTime = { value: 0 };

export function startFramesInterval() {
  interval = setInterval(() => {
    frames.forEach((i) => {
      timeouts.push(setTimeout(() => {
        clear(ctx2);
        ctx2.putImageData(i, 0, 0);
      }, timeoutTime.value));
      timeoutTime.value += intervalTime.value;
    });
  }, 0);
}

export function resetFramesInterval() {
  clearInterval(interval);
  for (let i = 0; i < timeouts.length; i += 1) {
    clearTimeout(timeouts[i]);
  }
}

export function restoreInterval() {
  intervalTime.value = Math.floor(1000 / fpsSlider.value);
  timeoutTime.value = 0;
}

export default function updateInterval() {
  restoreInterval();
  resetFramesInterval();
  startFramesInterval();
}
