import { frames, updateFramesNumbers } from './editFrames';
import updateInterval from '../preview/preview';


const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');
let dragSrcEl = null;

function handleDragStart(e) {
  this.style.opacity = '0.4';
  dragSrcEl = this;
  e.dataTransfer.effectAllowed = 'move';
  e.dataTransfer.setData('text/html', this.innerHTML);
}

function handleDragOver(e) {
  if (e.preventDefault) e.preventDefault();
  e.dataTransfer.dropEffect = 'move';
  return false;
}

function handleDragEnter() {
  this.classList.add('over');
}

function handleDragLeave() {
  this.classList.remove('over');
}

function handleDrop(e) {
  if (e.stopPropagation)e.stopPropagation();
  if (dragSrcEl !== this) {
    const number1 = dragSrcEl.firstChild.getAttribute('number');
    const context1 = dragSrcEl.firstChild.getContext('2d');
    const imageData1 = context1.getImageData(0, 0, canvas.width, canvas.height);
    const number2 = this.firstChild.getAttribute('number');
    const context2 = this.firstChild.getContext('2d');
    const imageData2 = context2.getImageData(0, 0, canvas.width, canvas.height);

    document.querySelectorAll('.active-frame').forEach(i => i.classList.remove('active-frame'));
    this.firstChild.classList.add('active-frame');

    context1.putImageData(imageData2, 0, 0);
    context2.putImageData(imageData1, 0, 0);
    ctx.putImageData(imageData1, 0, 0);

    frames[number1 - 1] = imageData2;
    frames[number2 - 1] = imageData1;
    updateFramesNumbers();
    updateInterval();
  }

  return false;
}

function handleDragEnd() {
  document.querySelector('.last-frame').classList.remove('last-frame');
  document.querySelector('.active-frame').classList.add('last-frame');
  const items = document.querySelectorAll('.frame-item');
  this.style.opacity = '1';
  [].forEach.call(items, (i) => {
    i.classList.remove('over');
  });
}

export default function dragAndDropListeners(el) {
  el.addEventListener('dragstart', handleDragStart, false);
  el.addEventListener('dragenter', handleDragEnter, false);
  el.addEventListener('dragover', handleDragOver, false);
  el.addEventListener('dragleave', handleDragLeave, false);
  el.addEventListener('drop', handleDrop, false);
  el.addEventListener('dragend', handleDragEnd, false);
}
