const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');
let isMouseDown = false;

function componentToHex(c) {
  const hex = c.toString(16);
  return hex.length === 1 ? `0${hex}` : hex;
}

function rgbToHex(rgb) {
  return `#${componentToHex(rgb[0])}${componentToHex(rgb[1])}${componentToHex(rgb[2])}`;
}

function getPixelAndLighten(x, y, lighten) {
  const posX = Math.trunc(x / 20);
  const posY = Math.trunc(y / 20);
  const imgd = ctx.getImageData(x / 5, y / 5, 1, 1);
  const currentColor = imgd.data.slice(0, 3);

  if (currentColor.every(i => i === 85) || currentColor.every(i => i === 76)) return;

  for (let i = 0; i < currentColor.length; i += 1) {
    if (lighten) currentColor[i] += 15;
    else currentColor[i] -= 15;
  }

  ctx.fillStyle = rgbToHex(currentColor);
  ctx.fillRect(posX * 4, posY * 4, 4, 4);
}

function lightenDarkenCheck() {
  const lighten = document.querySelector('.lighten').classList.contains('active-tool');
  const darken = document.querySelector('.darken').classList.contains('active-tool');
  if (lighten || darken) return true;
  return false;
}

export default function lightenDarken() {
  canvas.addEventListener('mousedown', () => {
    if (lightenDarkenCheck()) isMouseDown = true;
  });

  canvas.addEventListener('mouseup', () => {
    if (!lightenDarkenCheck()) return;
    isMouseDown = false;
    ctx.beginPath();
  });

  canvas.addEventListener('mousemove', (e) => {
    const lighten = document.querySelector('.lighten').classList.contains('active-tool');
    if (lightenDarkenCheck() && isMouseDown) {
      getPixelAndLighten(e.offsetX, e.offsetY, lighten);
    }
  });
}
