import {
  frames, framesCount, updateFramesNumbers, clear, addContextForActiveFrame,
} from './editFrames';
import updateInterval from '../preview/preview';
import dragAndDropListeners from './dragAndDrop';


const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');
const canvas2 = document.querySelector('.canvas2');
const ctx2 = canvas2.getContext('2d');
const framesList = document.querySelector('.frames-list');

export function deleteFrame(node) {
  const number = node.firstChild.getAttribute('number');
  frames.splice(number - 1, 1);
  framesList.removeChild(node);
  const activeFrame = document.querySelector('.active-frame');
  if (activeFrame) activeFrame.classList.remove('active-frame');
  if (framesList.childElementCount < 2) {
    frames = [];
    updateInterval();
    clear(ctx);
    clear(ctx2);
    return;
  }

  framesList.lastChild.firstChild.classList.add('active-frame');
  const context = framesList.lastChild.firstChild.getContext('2d');
  const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
  ctx.putImageData(imageData, 0, 0);
  framesCount.count -= 1;
  updateInterval();
  updateFramesNumbers();
  framesList.lastChild.firstChild.classList.add('last-frame');
}

function createFrame() {
  const div = document.createElement('div');
  const canv = document.createElement('canvas');
  const span = document.createElement('span');
  const bin = document.createElement('img');
  const clone = document.createElement('img');

  div.classList.add('frame-item');
  div.setAttribute('draggable', 'true');
  canv.classList.add('frame-canvas');
  canv.setAttribute('number', `${framesCount.count + 1}`);
  span.classList.add('frame-number');
  bin.classList.add('bin');
  bin.setAttribute('title', 'Delete frame');
  clone.classList.add('clone');
  clone.setAttribute('title', 'Clone frame');
  span.innerHTML = `${framesCount.count + 1}`;

  if (framesCount.count > 0) {
    document.querySelector('.active-frame').classList.remove('active-frame');
    document.querySelector('.last-frame').classList.remove('last-frame');
  }

  canv.classList.add('active-frame');
  canv.classList.add('last-frame');
  canv.width = 128;
  canv.height = 128;
  dragAndDropListeners.call(div, div);
  div.appendChild(canv);
  div.appendChild(span);
  div.appendChild(bin);
  div.appendChild(clone);
  clear(canv.getContext('2d'));
  return div;
}

export function cloneFrame(node, context) {
  const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
  const cloneContext = node.firstChild.getContext('2d');
  const tempFrame = framesList.lastChild;
  const tempImageData = frames.pop();
  const activeFrame = document.querySelector('.active-frame');

  cloneContext.putImageData(imageData, 0, 0);
  framesList.removeChild(tempFrame);
  framesList.appendChild(tempFrame);
  framesList.appendChild(node);
  frames.push(imageData);
  frames.push(tempImageData);
  if (activeFrame) activeFrame.classList.remove('active-frame');
  node.firstChild.classList.add('active-frame');
  document.querySelector('.last-frame').classList.remove('last-frame');
  node.firstChild.classList.add('last-frame');
  framesCount.count += 1;
  updateInterval();
  updateFramesNumbers();
}

export function addFrameToFramesList() {
  framesList.appendChild(createFrame());
  clear(ctx);
  clear(ctx2);
}

function editFrame() {
  const number = document.querySelector('.active-frame').getAttribute('number');
  const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  frames[number - 1] = imageData;
  updateInterval();
  addContextForActiveFrame();
}

export function pushTemporaryFrame() {
  if (canvas.classList.contains('edited')) {
    editFrame();
    return;
  }

  frames.pop();
  const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  frames.push(imageData);
  updateInterval();
  addContextForActiveFrame();
}

export function pushFrame() {
  frames.pop();
  let lastFrame = document.querySelector('.last-frame');
  if (!lastFrame) return;
  if (lastFrame !== framesList.lastChild.firstChild) lastFrame = framesList.lastChild.firstChild;
  const context = lastFrame.getContext('2d');
  let imageData = context.getImageData(0, 0, canvas.width, canvas.height);

  frames.push(imageData);
  framesCount.count += 1;
  clear(ctx);
  imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  frames.push(imageData);
  canvas.classList.remove('edited');
  updateInterval();
}

export default function startFrames() {
  addFrameToFramesList();
}
