import {
  intervalTime, timeoutTime, resetFramesInterval, startFramesInterval,
} from './preview';

const fpsSlider = document.querySelector('.fps-slider');

export default function startFpsSlider() {
  fpsSlider.addEventListener('change', () => {
    document.querySelector('.fps').innerHTML = `${fpsSlider.value} FPS`;
    intervalTime.value = Math.floor(1000 / fpsSlider.value);
    timeoutTime.value = 0;
    resetFramesInterval();
    startFramesInterval();
  });
}
