function activateTool(event) {
  document.querySelectorAll('.tools-item').forEach(i => i.classList.remove('active-tool'));
  document.querySelectorAll('.choose-color').forEach(i => i.classList.remove('active-tool'));
  document.querySelectorAll('.color-label').forEach(i => i.classList.remove('active-tool'));

  if (event.target.classList.contains('tools-item')) event.target.classList.add('active-tool');
}

export default function activeTool() {
  document.querySelector('.tools-list').addEventListener('click', activateTool);

  document.querySelector('.tools-list').addEventListener('mouseover', (e) => {
    if (e.target.classList.contains('tools-item')) {
      e.target.firstChild.style.display = 'block';
      e.target.addEventListener('mouseleave', () => {
        e.target.firstChild.style.display = 'none';
      });
    }
  });

  document.querySelector('.swap-colors').addEventListener('mouseover', () => {
    document.querySelector('.swap-colors-popup').style.display = 'block';
  });

  document.querySelector('.swap-colors').addEventListener('mouseleave', () => {
    document.querySelector('.swap-colors-popup').style.display = 'none';
  });
}
