let canvas;
let context;
let dragging = false;
let startPos;
let snapshot;
const figuresSet = new Set(['line', 'circle', 'rect', 'rhombus', 'triangle', 'semicircle-bottom', 'semicircle-top']);

function getCanvasCoordinates(event) {
  const x = event.clientX - canvas.getBoundingClientRect().left;
  const y = event.clientY - canvas.getBoundingClientRect().top;
  return { x, y };
}

function takeSnapshot() {
  snapshot = context.getImageData(0, 0, canvas.width, canvas.height);
}

function restoreSnapshot() {
  context.putImageData(snapshot, 0, 0);
}

function drawLine(position) {
  context.beginPath();
  context.moveTo(startPos.x, startPos.y);
  context.lineTo(position.x, position.y);
}

function drawCircle(position) {
  const radius = Math.sqrt(((startPos.x - position.x) ** 2) + ((startPos.y - position.y) ** 2));
  context.beginPath();
  context.arc(startPos.x, startPos.y, radius, 0, 2 * Math.PI, false);
}

function drawSemicircleTop(position) {
  const radius = Math.sqrt(((startPos.x - position.x) ** 2) + ((startPos.y - position.y) ** 2));
  context.beginPath();
  context.arc(startPos.x, startPos.y, radius, 0, Math.PI, true);
  context.closePath();
}

function drawSemicircleBottom(position) {
  const radius = Math.sqrt(((startPos.x - position.x) ** 2) + ((startPos.y - position.y) ** 2));
  context.beginPath();
  context.arc(startPos.x, startPos.y, radius, 0, Math.PI, false);
  context.closePath();
}

function drawRhombus(position, sides, angle) {
  const coordinates = [];
  let a = angle;
  const radius = Math.sqrt(((startPos.x - position.x) ** 2) + ((startPos.y - position.y) ** 2));
  let index = 0;

  for (index = 0; index < sides; index += 1) {
    coordinates.push({
      x: startPos.x + radius * Math.cos(a),
      y: startPos.y - radius * Math.sin(a),
    });
    a += (2 * Math.PI) / sides;
  }

  context.beginPath();
  context.moveTo(coordinates[0].x, coordinates[0].y);
  for (index = 1; index < sides; index += 1) {
    context.lineTo(coordinates[index].x, coordinates[index].y);
  }

  context.closePath();
}

function drawRect(position) {
  context.beginPath();
  context.rect(startPos.x, startPos.y, position.x - startPos.x, position.y - startPos.y);
}

function drawTriangle(position) {
  context.beginPath();
  context.moveTo(startPos.x, startPos.y);
  context.lineTo(position.x, position.y);
  context.lineTo(startPos.x + position.x, position.y);
  context.closePath();
}

function draw(position, shape) {
  context.beginPath();
  const pos = {};
  pos.x = Math.floor(position.x / 5);
  pos.y = Math.floor(position.y / 5);
  context.strokeStyle = document.querySelector('.color-label1').getAttribute('value');
  if (shape === 'circle') drawCircle(pos);
  if (shape === 'line' || shape === 'dashed-line') drawLine(pos);
  if (shape === 'rhombus') drawRhombus(pos, 4, Math.PI / 2);
  if (shape === 'rect') drawRect(pos);
  if (shape === 'triangle') drawTriangle(pos);
  if (shape === 'semicircle-bottom') drawSemicircleBottom(pos);
  if (shape === 'semicircle-top') drawSemicircleTop(pos);
  context.stroke();
}

function dragStart(event) {
  if (!document.querySelector('.active-tool')) return;
  const activeToolName = document.querySelector('.active-tool').getAttribute('name');
  if (!figuresSet.has(activeToolName)) return;
  dragging = true;
  startPos = getCanvasCoordinates(event);
  startPos.x = Math.floor(startPos.x / 5);
  startPos.y = Math.floor(startPos.y / 5);
  takeSnapshot();
}

function drag(event) {
  if (!document.querySelector('.active-tool')) return;
  const activeToolName = document.querySelector('.active-tool').getAttribute('name');
  if (!figuresSet.has(activeToolName)) return;
  let position;
  if (dragging === true) {
    const figureName = document.querySelector('.active-tool').getAttribute('name');
    restoreSnapshot();
    position = getCanvasCoordinates(event);
    draw(position, figureName);
  }
}

function dragStop(event) {
  if (!document.querySelector('.active-tool')) return;
  const activeToolName = document.querySelector('.active-tool').getAttribute('name');
  if (!figuresSet.has(activeToolName)) return;
  const figureName = document.querySelector('.active-tool').getAttribute('name');
  dragging = false;
  restoreSnapshot();
  const position = getCanvasCoordinates(event);
  draw(position, figureName);
  context.beginPath();
}

export default function startDrawFigures() {
  canvas = document.getElementById('canvas');
  context = canvas.getContext('2d');
  context.lineWidth = 4;
  canvas.addEventListener('mousedown', dragStart, false);
  canvas.addEventListener('mousemove', drag, false);
  canvas.addEventListener('mouseup', dragStop, false);
}
