const canvas = document.querySelector('.canvas');
const ctx = canvas.getContext('2d');
const canvas2 = document.querySelector('.canvas2');
const canvasPosition = {};
let x0 = null;
let x1 = null;
let y0 = null;
let y1 = null;
let coordinateX = null;
let coordinateY = null;
canvas.width = 128;
canvas.height = 128;
canvas2.width = 128;
canvas2.height = 128;
ctx.lineWidth = 4;

function ditheringPattern(color1, color2) {
  const canv = document.createElement('canvas');
  canv.width = 8;
  canv.height = 8;
  const context = canv.getContext('2d');
  context.fillStyle = color1;
  context.fillRect(0, 0, 4, 4);
  context.fillRect(4, 4, 4, 4);
  context.fillStyle = color2;
  context.fillRect(4, 0, 4, 4);
  context.fillRect(0, 4, 4, 4);
  const pattern = ctx.createPattern(canv, 'repeat');
  ctx.fillStyle = pattern;
}

function eraserPattern() {
  const img = document.querySelector('.bg');
  const pattern = ctx.createPattern(img, 'repeat');
  ctx.fillStyle = pattern;
}

function fillStyleChecker() {
  const activePen = document.querySelector('.pen').classList.contains('active-tool');
  const activeEraser = document.querySelector('.eraser').classList.contains('active-tool');
  const activeDithering = document.querySelector('.dithering').classList.contains('active-tool');
  const color1 = document.querySelector('.color-label1').getAttribute('value');
  const color2 = document.querySelector('.color-label2').getAttribute('value');

  if (activePen) ctx.fillStyle = color1;
  else if (activeEraser) eraserPattern();
  else if (activeDithering) ditheringPattern(color1, color2);
  else return false;
  return true;
}

function draw(e) {
  if (fillStyleChecker()) {
    x1 = e.pageX - canvasPosition.left;
    y1 = e.pageY - canvasPosition.top;
    const dx = Math.abs(x1 - x0);
    const dy = Math.abs(y1 - y0);
    const sx = x0 < x1 ? 1 : -1;
    const sy = y0 < y1 ? 1 : -1;
    let err = dx - dy;

    /* eslint-disable no-constant-condition */
    while (true) {
      coordinateX = Math.floor(x0 / 20);
      coordinateY = Math.floor(y0 / 20);

      ctx.fillRect(coordinateX * 4, coordinateY * 4, 4, 4);

      if (x0 === x1 && y0 === y1) break;
      const err2 = 2 * err;
      if (err2 > -dy) {
        err -= dy;
        x0 += sx;
      }
      if (err2 < dx) {
        err += dx;
        y0 += sy;
      }
    }

    return 1;
  }
  return false;
}

function cursorPosition(e) {
  const canvasX = Math.trunc(e.offsetX / 20) + 1;
  const canvasY = Math.trunc(e.offsetY / 20) + 1;
  document.querySelector('.coordinates').innerHTML = `x: ${canvasX}<br>y: ${canvasY}`;
}

function startDrawing(e) {
  canvasPosition.left = canvas.getBoundingClientRect().left;
  canvasPosition.top = canvas.getBoundingClientRect().top;
  ctx.beginPath();
  x0 = e.pageX - canvasPosition.left;
  y0 = e.pageY - canvasPosition.top;
  ctx.moveTo(x0, y0);
  canvas.addEventListener('mousemove', draw);
  canvas.addEventListener('click', draw);
}

function stopDrawing() {
  canvas.removeEventListener('mousemove', draw);
}

export default function startDraw() {
  canvas.addEventListener('mousemove', cursorPosition);
  canvas.addEventListener('mousedown', startDrawing);
  canvas.addEventListener('mouseout', stopDrawing);
  canvas.addEventListener('mouseup', stopDrawing);
}
